import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import CardLayout from "../../../components/Cards/CardLayout";
import { FormGroup, Col, Button, Label } from "reactstrap";
import DropdownSelect from "../../../components/InputElement/Dropdown";
import InputElement from "../../../components/InputElement/InputElement";

class CropsForm extends Component {
    constructor(props){
        super(props);
        this.state={
            updateFlag: false,
        }
    }
    co
  render() {
    return (
        <div style={{ marginTop: 30 }}>
        <CardLayout
          name="District Form"
          navigation={true}
          navigationRoute="/master/districts"
        >
          <FormGroup row />
          <div style={{ margin: 20 }}>
            <FormGroup row>
              <Col xs="10" md="5">
                <Label />
                <DropdownSelect
                  placeholder="Please select state"
                  options={this.props.statesList}
                  //onChange={this.onStateSelect.bind(this)}
                  //value={districtObj.StateName}
                />
              </Col>
              <Col xs="10" md="5">
                <InputElement
                  type="text"
                  placeholder="Please enter district"
                 // value={districtObj.DistrictName}
                />
              </Col>
            </FormGroup>
            <FormGroup row>
              {/* <Col md="3">
                {this.state.updateFlag ? (
                  <Button
                    className="theme-positive-btn"
                    //onClick={this.onSubmit.bind(this)}
                  >
                    Edit
                  </Button>
                ) : (
                  <Button
                    className="theme-positive-btn"
                    //onClick={this.onSubmit.bind(this)}
                  >
                    Submit
                  </Button>
                )}
              </Col> */}
            </FormGroup>
          </div>
        </CardLayout>
      </div>
    );
  }
}

export default CropsForm;


